<template>
  <div class="header">
    <div class="banxin">
      <swiper ref="swiper" :options="swiperOption" class="swiper-container">
        <swiper-slide v-for="item in swiperList" :key="item.id" :data-uri="item.id" :class="{'active': navActive === item.id}">
          {{ item.text }}
        </swiper-slide>
      </swiper>
      <!-- <ul>
        <li @click="navClick('index')" :class="{'active': navActive === 'index'}">Home</li>
        <li @click="navClick('meizi')" :class="{'active': navActive === '妹子'}">女团</li>
        <li @click="navClick('game')" :class="{'active': navActive === 'game'}">游戏</li>
        <li @click="navClick('dao')" :class="{'active': navActive === 'dao'}">悟道</li>
        <li @click="navClick('dir')" :class="{'active': navActive === 'dir'}">日志</li>
        <li @click="navClick('neighbor')" :class="{'active': navActive === 'neighbor'}">友人帐</li>
        <li @click="navClick('file')" :class="{'active': navActive === 'file'}">悠哉</li>
      </ul> -->
      <div class="logo">
        <div class="imgFoo"></div>
      </div>
    </div>
    
  </div>
</template>

<script>
import  { Swiper, SwiperSlide} from 'vue-awesome-swiper'
import 'swiper/swiper.scss'

let swiperList = [
  { id: 'index', text: 'Home' },
  { id: 'meizi', text: '女神' },
  { id: 'game', text: '公告/日志' },
  { id: 'dao', text: '配将/攻略' },
  { id: 'dir', text: '活动/宣传' },
  { id: 'neighbor', text: '时光轴' },
  { id: 'file', text: '友人帐' },
  { id: 'cia', text: 'CIA' },
]

export default {
  name: 'Header',
  components: {
    Swiper,
    SwiperSlide
  },
  data() {
    let navActive = location.pathname.split('/')[1]
    let initialSlide = swiperList.findIndex(item => item.id === navActive)
    return {
      navActive,
      swiperOption: {
        slidesPerView : 'auto',
        spaceBetween : 20,
        initialSlide,
        slidesOffsetAfter : 50,
        on: {
          click: (el) => {
            if(!el.changedTouches[0].target.dataset.uri) return
            this.$router.push({
              path: el.changedTouches[0].target.dataset.uri
            })
          }
        }
      },
      swiperList,
    }
  },
  created() {
    
  },
  mounted() {
  
  },
  watch: {
    $route: {
      handler(val) {
        let navActive = val.path.split('/')[1]
        if(!val) return
        this.navActive = navActive;
        
      },
      deep: true
    }
  },
  methods: {
    navClick(val) {
      this.navActive = val
      this.$router.push({
        path: val
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.header{
  width: 100%;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  margin-bottom: 10px;
  background: #fff;
  z-index: 9;
  .banxin{
    display: flex;
    align-items: center;
    padding: 0;
    box-shadow: 0 1px 2px 0 rgba(0,0,0,.05);
    .swiper-container{
      flex: 1;
      margin: 0 10px;
      .swiper-slide{
        width:auto;
        height: 45px;
        line-height: 45px;
        font-size: 16px;
      }
      .swiper-slide.active{
        border-bottom: 2px solid #333;
      }
    }
    // ul{
    //   flex: 1;
    //   display: flex;
    //   height: 45px;
    //   li{
    //     line-height: 45px;
    //     margin: 0 20px;
    //     font-size: 16px;
    //     cursor: pointer;
    //   }
    //   li.active{
    //     border-bottom: 2px solid #333;
    //   }
    //   li:nth-child(1) {
    //     margin-left: 0;
    //   }
    // }
    .logo{
      position: absolute;
      right: 10px;
      top: 0;
      width: 45px;
      height: 45px;
      background: #fff;
      z-index: 1;
      .imgFoo{
        width: 100%;
        height: 40px;
        background: url(~@/assets/img/logo.gif) no-repeat center;
        background-size: auto 40px;
        border-radius: 50%;
      }
    }
  }
}

</style>
