import Vue from 'vue';
import Router from 'vue-router';

import HomeRouters from './modules/home.js'
import MeiziRouters from './modules/meizi.js'
import GameRouters from './modules/game.js'
import DaoRouters from './modules/dao.js'
import DirRouters from './modules/dir.js'
import NeighborRouters from './modules/neighbor.js'
import FileRouters from './modules/file.js'
import CiaRouters from './modules/cia.js'

Vue.use(Router);

const router = new Router({
  routes: [
    {
      path: '/',
      redirect: {
        name: 'home',
      }
    },
    ...HomeRouters,
    ...MeiziRouters,
    ...GameRouters,
    ...DaoRouters,
    ...DirRouters,
    ...NeighborRouters,
    ...FileRouters,
    ...CiaRouters
  ],
  mode: 'history'
})

export default router;